/**
 * 
 */
export default {
    /**
     * 
     */
    ALL_COMPS: {},

    /**
     *  id
     * 
     * @returns  id
     */
    nextId(): number {
        return ++uid;
    },

    /**
     * 
     * 
     * @param vueObj 
     */
    register(comp: Component): void {
        let id: string = "ajComp-" + comp.id;
        // comp.svg.node.id = id;

        let w: Warpper = <Warpper><unknown>comp;
        w.svg.node.id = id;

        this.ALL_COMPS[id] = comp;
    },

    /**
     * 
     * 
     * @param id 
     */
    unregister(id: number): void {
        delete this.ALL_COMPS[id];
    }
}

/**
* id 
*/
let uid: number = 0;