import { BaseState } from "../state/base";

/**
 * 
 */
export class ResizeControl {
    /**
     * 
     */
    private isBorder: boolean = true;

    /**
     * 
     */
    private rectComp: BaseState;

    /**
     * 
     */
    private vBox: VBox;

    /**
     *  map
     */
    private allDots: { [key: string]: any } = {};

    /**
     * 0=
     */
    private static margin: number = 10;

    private border: any;

    private dotX: number = 0;

    private dotY: number = 0;

    /**
     *  Resize 
     * 
     * @param rectComp 
     */
    constructor(rectComp: BaseState) {
        this.rectComp = rectComp;
        this.vBox = rectComp.vBox; // 
    }

    /**
     * 
     */
    renderer(): void {
        let allDots: { [key: string]: any } = {}, //  map
            self = this;

        this.allDots = allDots;
        this.dotX = this.dotY = 0;

        let bdragStart = function (this: Raphael) {//  fnwhy
            self.dotX = Number(this.attr('x'));
            self.dotY = Number(this.attr('y'));
        }

        let PAPER = this.rectComp.PAPER;
        allDots['t'] = PAPER.rect().addClass('resizeDot').attr({ cursor: 's-resize' }).drag((dx: number, dy: number) => this.dotMove(dx, dy, 't'), bdragStart);     // 
        allDots['lt'] = PAPER.rect().addClass('resizeDot').attr({ cursor: 'nw-resize' }).drag((dx: number, dy: number) => this.dotMove(dx, dy, 'lt'), bdragStart);  // 
        allDots['l'] = PAPER.rect().addClass('resizeDot').attr({ cursor: 'w-resize' }).drag((dx: number, dy: number) => this.dotMove(dx, dy, 'l'), bdragStart);     // 
        allDots['lb'] = PAPER.rect().addClass('resizeDot').attr({ cursor: 'sw-resize' }).drag((dx: number, dy: number) => this.dotMove(dx, dy, 'lb'), bdragStart);  // 
        allDots['b'] = PAPER.rect().addClass('resizeDot').attr({ cursor: 's-resize' }).drag((dx: number, dy: number) => this.dotMove(dx, dy, 'b'), bdragStart);     // 
        allDots['rb'] = PAPER.rect().addClass('resizeDot').attr({ cursor: 'se-resize' }).drag((dx: number, dy: number) => this.dotMove(dx, dy, 'rb'), bdragStart);  // 
        allDots['r'] = PAPER.rect().addClass('resizeDot').attr({ cursor: 'w-resize' }).drag((dx: number, dy: number) => this.dotMove(dx, dy, 'r'), bdragStart);     // 
        allDots['rt'] = PAPER.rect().addClass('resizeDot').attr({ cursor: 'ne-resize' }).drag((dx: number, dy: number) => this.dotMove(dx, dy, 'rt'), bdragStart);  // 

        this.hideBox();
        this.setDotsPosition();
        this.resize();
    }

    /**
     * 
     * @param isBorder 
     */
    enableBorder(isBorder: boolean): void {
        this.isBorder = isBorder;
    }

    /**
     * 
     */
    setDotsPosition(): void {
        let vBox: VBox = this.vBox,
            x: number = vBox.x - ResizeControl.margin,
            y: number = vBox.y - ResizeControl.margin,
            width: number = vBox.width + ResizeControl.margin * 2,
            height: number = vBox.height + ResizeControl.margin * 2,
            _bw: number = 2.5,
            allDots: { [key: string]: any } = this.allDots;

        allDots['t'].attr({ x: x + width / 2 - _bw, y: y - _bw }); 			// 
        allDots['lt'].attr({ x: x - _bw, y: y - _bw }); 				    // 
        allDots['l'].attr({ x: x - _bw, y: y - _bw + height / 2 }); 	    // 
        allDots['lb'].attr({ x: x - _bw, y: y - _bw + height }); 	        // 
        allDots['b'].attr({ x: x - _bw + width / 2, y: y - _bw + height }); // 
        allDots['rb'].attr({ x: x - _bw + width, y: y - _bw + height }); 	// 
        allDots['r'].attr({ x: x - _bw + width, y: y - _bw + height / 2 }); // 
        allDots['rt'].attr({ x: x - _bw + width, y: y - _bw }); 			// 
    }

    /**
     * 
     */
    private resize(): void {
        this.setDotsPosition();
        this.rectComp.svg.attr(this.vBox);
        this.updateBorder();
    }

    /**
     * 
     */
    showBox(): void {
        this.border && this.border.show();  // 

        for (let i in this.allDots) // 
            this.allDots[i].show();
    }

    /**
     * 
     */
    hideBox(): void {
        this.border && this.border.hide();

        for (let i in this.allDots)
            this.allDots[i].hide();
    }

    /**
     * 
     * @param dx 
     * @param dy 
     * @param type 
     */
    private dotMove(dx: number, dy: number, type: string): void {
        let x: number = this.dotX + dx,
            y: number = this.dotY + dy,
            vBox: VBox = this.vBox;

        // console.log('-----------------') console.log(this.dotY) console.log(dy)
        switch (type) {
            case 't':
                // console.log(vBox.y) console.log(y) console.log(vBox.y - y)
                vBox.height += vBox.y - y; vBox.y = y;
                break;
            case 'lt':
                vBox.width += vBox.x - x; vBox.height += vBox.y - y;
                vBox.x = x; vBox.y = y;
                break;
            case 'l':
                vBox.width += vBox.x - x;
                vBox.x = x;
                break;
            case 'lb':
                vBox.height = y - vBox.y; vBox.width += vBox.x - x;
                vBox.x = x;
                break;
            case 'b':
                vBox.height = y - vBox.y;
                break;
            case 'rb':
                vBox.height = y - vBox.y; vBox.width = x - vBox.x;
                break;
            case 'r':
                vBox.width = x - vBox.x;
                break;
            case 'rt':
                vBox.width = x - vBox.x; vBox.height += vBox.y - y;
                vBox.y = y;
                break;
        }

        this.resize();
    }

    /**
     * 
     */
    updateBorder(): void {
        if (!this.isBorder)
            return;

        if (!this.border)
            this.border = this.rectComp.PAPER.path('M0 0L1 1').hide(); // 

        let vBox: VBox = this.vBox,
            x: number = vBox.x - ResizeControl.margin,
            y: number = vBox.y - ResizeControl.margin,
            width: number = vBox.width + ResizeControl.margin * 2,
            height: number = vBox.height + ResizeControl.margin * 2,
            str = 'M' + x + ' ' + y + 'L' + x + ' ' + (y + height) + 'L' + (x + width) + ' ' + (y + height) + 'L' + (x + width) + ' ' + y + 'L' + x + ' ' + y;

        this.border.attr({ path: str });
    }
}
